.App {
  text-align: center;
}

.list-view {
  display: grid;
  grid-template-columns: repeat(auto-fit, minmax(200px, 1fr));
  grid-gap: 16px;
  margin-top: 16px;
}

.user-item {
  background-color: #f5f5f5;
  border-radius: 8px;
  padding: 16px;
  margin-bottom: 16px;
}

.avatar-container {
  display: flex;
  align-items: center;
  justify-content: center;
  position: relative;
  margin-bottom: 8px;
}

.user-avatar {
  width: 100px;
  height: 100px;
  border-radius: 50%;
  object-fit: cover;
}

.user-id {
  position: absolute;
  top: 8px;
  right: 8px;
  background-color: #ffffff;
  padding: 4px 8px;
  border-radius: 4px;
  font-size: 14px;
  font-weight: bold;
}

.user-name {
  font-size: 18px;
  font-weight: bold;
  margin-bottom: 8px;
}

input {
  margin-bottom: 16px;
  padding: 8px;
  border: 1px solid #994949;
  border-radius: 4px;
}
